.Start {
  /* background-color: #5da8d6; */
  background-color: #ebe8e8;
}

.Wrapper {
  width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-areas:
    "h h"
    "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 15px;
}

.Content {
  grid-area: c;
  background-color: white;
  border-radius: 5px;
  margin-bottom: 20px;
}

a {
  text-decoration: none;
}

button {
  background-color: #79aecf;
  border: none;
  color: white;
  cursor: pointer;
  padding: 5px;
  border-radius: 3px;
  outline: none;
}

textarea {
  background-color: white;
  border-radius: 3px;
  outline: none;
  resize: none;
  /* border: none; */
}

::placeholder {
  padding-left: 2px;
}

.error {
  color: red;
  margin-top: 10px;
  margin-bottom: 10px;
}
