.Nav {
  grid-area: n;
  background-color: white;
  height: 130px;
  border-radius: 4px;
  box-shadow: 0px 0px 1px 0px black;
  margin-bottom: 20px;
}

.item {
  font-size: 22px;
  padding-left: 5px;
  letter-spacing: 1px;
  display: inline-block;
  margin-left: 5px;
  font-size: 24px;
}

.activeLink {
  color: #929258;
  padding: 5px;
  border-radius: 6px 0px 0px 6px;
}

.item:hover {
  color: rgb(83, 117, 83);
}

.item:active {
  color: rgb(64, 88, 64);
}

.Nav img {
  width: 17px;
  margin-left: 7px;
}

.items {
  margin-top: 5px;
}
