.MyPosts {
  background-color: white;
  border-radius: 4px;
  padding: 10px;
  padding-top: 0px;
  box-shadow: 0px 0px 1px 0px black;
  float: right;
  width: 730px;
}

.MyPostsForm {
  float: right;
  margin-bottom: 15px;
}

.MyPosts .Publickbutton {
  padding: 5px;
}

.textareaButton {
  padding: 15px;
  /* background-color: white; */
  height: 15px;
  border-radius: 4px;
  float: right;
  /* background-color: rgb(221, 239, 255); */
  margin-bottom: 20px;
  float: right;
}

.textareaButtonBlock {
  display: grid;
  gap: 15px;
  grid-template-columns: 2fr 10fr;
  box-shadow: 0px 0px 1px 0px black;
  padding: 15px;
  background-color: white;
  border-radius: 4px;
  width: 720px;
}

.textareaButton textarea {
  float: left;
  border: none;
  background-color: rgb(221, 239, 255);
}

.Post {
  padding-top: 50px;
}

.buttonForm {
  margin-left: 10px;
  margin-top: 5px;
}

.textareaButtonBlock img {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  display: inline-block;
}
