.header {
  grid-area: h;
  background-color: white;
  /* box-shadow: 0 0 1px 0 black; */
  border-radius: 0px 0px 4px 3px;
  /* box-shadow: 0px 0px 1px 0px black; */
}

h1 {
  margin: auto;
  font-size: 30px;
  padding-left: 10px;
  padding-top: 6px;
  color: #4c88ad;
  display: inline-block;
}

.logBlock {
  float: right;
  padding-right: 10px;
  margin-top: 15px;
  margin-right: 15px;
}

.header img {
  height: 45px;
  margin-top: 5px;
  margin-left: 19px;
}

.header h1 span {
  font-size: 20px;
  padding-left: 300px;
}

.out {
  color: #5395be;
  cursor: pointer;
  margin-right: 5px;
  margin-bottom: 10px;
  display: inline;
}

.out:hover {
  color: #407ba0;
}
